import * as React from "react";
import { SearchBar, ListView, Toast } from 'antd-mobile'
import $ from 'jquery'

import List from '../../components/consumer/list'
import FilterComponent from '../../components/search/filterComponent'
import { getConsumerList } from '../../api/consumer'
import Blank from '../../components/blank/blank'



import './consumer.less'

interface ISprops {
}
interface IState {
    filter: any,
    query: any,
    isLoading: any,
    hasMore: any,
    dataSource: any
}

export default class BindPhone extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
        this.state = {
            filter: {
                erp_card_ids: '',
                start_time: '',
                end_time: '',
                series_id: ''
            },
            query: '',
            isLoading: false,
            hasMore: false,
            dataSource
        }
    }
    courseList: Object[] = [];
    page_num: number = 1;
    page_size: number = 10;
    // 
    handleFilterChange = (filters) => {
        let obj = {};
        let arr = Object.keys(filters)
        arr.map((el, index) => {
            if (el !== "citys") {
                obj[el] = filters[el]
            }
        })
        this.setState({
            filter: { ...obj }
        }, () => {
            this.page_num = 1;
            this.getConsumer();
        })
    }
    getConsumer = () => {
        this.setState({ isLoading: true });
        Toast.loading('');
        let { page_num, page_size } = this;
        let { filter, query } = this.state;
        getConsumerList({
            page_num,
            page_size,
            ...filter,
            course_name: query

        }).then(res => {
            Toast.hide()
            if (!res.status) {
                let { list, total_pages, page_num } = res.data;
                // 
                if (page_num === 1) {
                    this.courseList = []
                }
                this.courseList = [...this.courseList, ...list];

                // 1
                if (page_num < total_pages) {
                    this.page_num += 1
                }

                this.setState({
                    dataSource: this.state.dataSource.cloneWithRows(this.courseList),
                    isLoading: false,
                    hasMore: page_num < total_pages
                })
            }
        })
    }
    onEndReached = () =>{
        if (this.state.isLoading || !this.state.hasMore) {
            return;
        }
        this.getConsumer()
    }
    clearQuery = () => {
        //searchbar 
        $('.am-search-synthetic-ph-placeholder').css({ 'color': '#bbb' })
        this.setState({
            query: ''
        })
    }
    handleSubmit = (v) => {
        this.page_num = 1;
        this.getConsumer()
    }
    changeBlur = () => {
        this.clearQuery()
    }
    changeSearch = (e) => {
        this.setState({
            query: e
        })
        $('.am-search-synthetic-ph-placeholder').css({ 'color': '#EDEDED' })
    }
    changeCancel = () => {
        this.clearQuery()
    }
    componentWillMount() {
        document.title = '';
        this.getConsumer()
    }
    render() {
        const { filter, query, isLoading } = this.state;
        const { courseList } = this;
        const renderFooter = () => (
            this.state.hasMore
                ?
                <div style={{ textAlign: 'center', lineHeight: '.26rem' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>
                :
                <div></div>
        )
        const Row = (rowData, sectionID, rowID) => (
            <List key={rowID} consumer={rowData} />
        )
        return (
            <div className="consumer" style={{ paddingTop: '.9rem' }}>
                <div className="search-top">
                    <SearchBar
                        value={query}
                        placeholder=""
                        onSubmit={(val) => this.handleSubmit(val)}
                        onBlur={this.changeBlur}
                        className="search-bar"
                        onChange={(e) => this.changeSearch(e)}
                        showCancelButton={false}
                        onCancel={this.changeCancel}
                    />
                    <FilterComponent 
                        filters={filter} 
                        onChange={this.handleFilterChange} 
                        consumerFlag={true} 
                        cardTit=''
                        calendarTit=''
                        seriesTit=''
                    />
                </div>
                {
                    !isLoading && courseList.length === 0 ? 
                        <Blank/>
                        :
                        <ListView
                            dataSource={this.state.dataSource}
                            renderFooter={courseList.length === 0 ? () => (<div></div>) : renderFooter}
                            renderRow={Row}
                            className="consumer-index"
                            pageSize={4}
                            useBodyScroll
                            scrollRenderAheadDistance={500}
                            onEndReached={this.onEndReached}
                            onEndReachedThreshold={10}
                        />
                }
                

            </div>
        )
    }

}
